<template>
    <header class="fixed-top">
      <nav class="navbar navbar-expand-lg pl-5 pr-5">
        <router-link class="navbar-brand" to="/">
          <img src="../assets/images/logo.svg" alt="">
        </router-link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <i class="fa fa-bars fa-2x text-white" @click="menuShow = !menuShow"></i>
        </button>
        <div :class="{'show': menuShow}" class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav nav ml-auto">
            <li class="nav-item">
              <router-link class="nav-link" to="/"></router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/services"></router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/projects"></router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/about-us"> </router-link>
            </li>
          </ul>
        </div>
      </nav>
    </header>
</template>

<script>
  export default {
    data () {
      return {
        menuShow: false
      };
    },
    mounted() {
      $(window).scroll(function () {
        if  ($(window).scrollTop() > 50) {
          $('.navbar').addClass('active');
        } else {
          $('.navbar').removeClass('active');
        }
      })
    }
  }
</script>

<style scoped>

</style>


