const Home = () => import('../layouts/Home');
const Services = () => import('../layouts/Services');
const Project = () => import('../layouts/Project');
const About = () => import('../layouts/About');



export const routes = [
  {path: '/', component: Home},
  {path: '/services', component: Services},
  {path: '/projects', component: Project},
  {path: '/about-us', component: About}
];



